.chatbox-container {
  /* position: relative; */
  background-color: rgba(0, 0, 0, 0.301);
  width: 100vw;
  height: 120vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 3;
  transition: all 300ms ease;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}

.chatbox-main {
  width: 500px;
  height: calc(100vh - 70px);
  position: fixed;
  top: 70px;
  right: 0;
  background: #f6fbfd;
  z-index: 12;
  transition: all 400ms ease;
  /* padding-top: ; */
}

.hideChatBox {
  transform: translateX(500px);
  transition: all 400ms ease;
}

.hideChatBoxOverlay {
  transform: translateX(100vw);
  background-color: transparent;
  transition: all 500ms ease;
  /* position: relative;
    width: 0; */
}

.chatbox-main-inner {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.chatboxHeader {
  width: 100%;
  height: 12%;
  background: linear-gradient(90deg, #82cff0 0%, #1997cf 100%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
}

.chatboxHeader .chatboxHeaderCol1 {
  display: flex;
  padding-top: 10px;
}

.chatboxHeader .chatboxHeaderCol1 p {
  margin: 0;
  padding: 0;
  font-weight: bold;
  font-size: 15px;
  color: #ffffff;
}

.chatboxHeader .chatboxHeaderCol1 svg {
  margin-right: 10px;
}

.chatBoxBody {
  width: 100%;
  height: 73%;
  padding-left: 20px;
  padding-right: 20px;
  overflow-y: scroll;
  padding-top: 20px;
}

.chatBoxInputBox {
  width: 100%;
  height: 15%;
  background-color: white;
  border-top: 1px solid rgba(128, 128, 128, 0.452);
}

.chatBoxInputBox form {
  width: 100%;
  height: 100%;
  border: none;
  display: flex;
}

.chatBoxInputBox form textarea {
  width: 75%;
  height: 100%;
  outline: none;
  padding: 10px;
  font-size: 13px;
  border: none;
  /* box-shadow: inset 0px 1px 0px rgba(0, 0, 0, 0.15); */
  padding-bottom: 30px;
  padding-top: 10px;
  color: rgba(14, 86, 118, 0.4);
}

.chatBoxInputBox form .chatBoxActionSend {
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 10px;
}

.chatBoxInputBox form .chatBoxActionSend button {
  width: 75%;
  height: 44px;
  background: #1997cf;
  border-radius: 8px;
  border: none;
  color: white;
  margin-left: 10px;
}

.cursor {
  cursor: pointer !important;
}

.botDefaultMsg {
  display: flex;
  margin-bottom: 10px;
}

.botDefaultMsgCol2 {
  background-color: white;
  border-radius: 10px;
  padding: 20px;
}

.askNelloOptions {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

.askNelloOptionsGrid {
  border: 1px solid #def2fb;
  border-radius: 8px;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  cursor: pointer;
  transition: all 300ms ease;
  padding-bottom: 5px;
}

.askNelloOptionsGrid:hover {
  border: 1px solid #1998cf80;
}

.askNelloOptionsGrid img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}

.askNelloOptionsGrid p {
  color: #1997cf;
  font-size: 12px;
  padding-top: 10px;
}

.botDefaultMsgCol2Header p {
  color: #0e5676;
  font-size: 14px;
}

.botDefaultMsgCol1 {
