  border-radius: 50%;
  margin-right: 10px;
}

.botDefaultMsgCol1 img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.incomingMsgWrap {
  background: #ffffff;
  box-shadow: 0px 20px 100px rgba(11, 68, 93, 0.04);
  border-radius: 0px 30px 30px 30px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px;
  padding-right: 20px;
  padding-top: 20px;
  margin-bottom: 7px;
  margin-top: 7px;
  transition: all 300ms ease;
  padding: 20px;
